*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: 300px;
}

body {
  color: rgba(0, 0, 0, 0.856);
  margin: 0;
  background-image: repeating-radial-gradient(
    circle,
    rgb(163, 162, 162) 0px,
    rgb(163, 162, 162) 0px,
    transparent 2px,
    transparent 100%
  );
  background-color: rgba(240, 240, 240, 0.5);
  background-size: 38px 38px;
}

.country {
  padding: 10px;
}

.country__title {
  text-align: center;
  font-size: 70px;
}

.country__subtitle,
.language-list {
  padding: 0;
  list-style-position: inside;
  font-weight: 700;
  text-align: left;
  font-size: 40px;
  margin-top: 0;
  margin-bottom: 25px;
}

.language-list__item {
  font-size: 30px;
  padding-left: 35px;
  margin-top: 20px;
  font-weight: 400;
  line-height: 1.5;
}

.country__value {
  font-size: 35px;
  font-weight: 400;
}

.country__info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 35px;
}

.input {
  margin: 30px;
  padding-left: 20px;
  width: 450px;
  height: 70px;
  border: 1px solid grey;
  border-radius: 6px;
  font-size: 30px;
  color: rgb(7, 7, 65);
}

.input:hover,
.input:focus {
  border: 2px solid blue;
}

.country__flag {
  box-shadow: 0 0 20px 10px rgba(146, 173, 248, 0.432);
}

.not-found {
  font-weight: 700;
  font-style: italic;
  padding-left: 30px;
  color: red;
  font-size: 35px;
}
